import React, { Component } from 'react';

class Home extends Component {    
  
  handleSubmit = (e) => {
    e.preventDefault();
    let teacherName = this.name.value;
    let teacherTopic = this.topic.value;
    let path = `teachers/${teacherTopic}/${teacherName}`;
    this.props.history.push(path); 
  }
  
  render() {
    return (
      <div className="main-content home">
      <hr />
        
        
        <h2>FIND A CLASS</h2>
        <p>If you have a desire to get <em>fit, stay fit,</em>  or simply get your endorphins going, let's do it.</p>
        <p>And if you're on the fence for some crazy reason, why not take advantage of our Free Day Pass?</p>
        <p>Good at any of our gyms, with not one string attached.</p>
        <p>Our Group Exercise classes set the bar for choice and intensity, and they're always free. From the classics like Pilates to the latest workout rage, we've got it.</p>
        <hr />
        <p>Featured Instructors</p>
        
        <hr />
            <ul>
             <li>Mike Stankus</li>
             <li>Katee McDowell</li>
             <li>Ellen Pryde</li>
             <li>Alan Garcia</li>
             <li>Chris Delluca</li>
             <li>Matt Dowell</li>
           </ul>
        <hr />

        <p>Featured Classes</p>
        <hr />
        <ul>
             <li>Cardio Training</li>
             <li>Resistance Training</li>
             <li>Yoga</li>
             
           </ul>
        <hr />
        <form onSubmit={this.handleSubmit}>
          <input type="text" placeholder="Name" ref={ (input) => this.name = input } />
          <input type="text" placeholder="Topic" ref={ (input) => this.topic = input } />
          <button type="submit">Go!</button>
        </form>
      </div>
    );
  }
}

export default Home;



